.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;
}

.article {
  margin: 0;
  margin-bottom: 15px;
}

.article__term {
  display: inline;
  color: #005aa2;
}

.article__definition {
  display: inline;
  font-weight: 700;
  margin: 0;
}

.button {
  text-decoration: none;
  color: inherit;
  cursor: pointer;
  font-family: inherit;
  font-size: 16px;
  font-weight: 700;
  color: #fff;
  background-position: 25px center;
  background-repeat: no-repeat;
  text-align: end;
  border: none;
  padding: 15px;
  border-radius: 5px;
}
.button--submit {
  background-color: #005aa2;
  background-image: url("../img/shopping_cart.png");
  padding-right: 19px;
}
.button--tel {
  display: block;
  background-color: #00c232;
  background-image: url("../img/phone_in_talk.png");
}
.button--favorite {
  color: #656565;
  background-color: #f6f6f6;
  background-position: 10px center;
  border: 2px solid #e7e7e7;
  background-image: url("../img/favorite_border.png");
  text-align: end;
}
.button:hover {
  opacity: 0.5;
}
.button:active {
  opacity: 0.5;
  color: #000;
}

.breadcrumbs__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
}

.breadcrumbs__item {
  position: relative;
}

.breadcrumbs__item:not(:last-child)::after {
  content: "";
  position: absolute;
  top: 50%;
  right: -12px;
  transform: translateY(-50%);
  display: block;
  background-image: url("../img/radio_checked.png");
  background-repeat: no-repeat;
  width: 8px;
  height: 8px;
  background-size: 8px 8px;
}

.breadcrumbs__link {
  text-decoration: none;
  color: inherit;
  cursor: pointer;
  font-size: 16px;
  color: #d5d5d5;
}
.breadcrumbs__link--current {
  font-weight: 700;
  color: #000;
}
.breadcrumbs__link:hover {
  color: #656565;
}
.breadcrumbs__link:active {
  color: #000;
}

.description {
  padding-left: 10px;
  margin-bottom: 10px;
}

.description__list {
  font-size: 16px;
  margin: 0;
}

.description__title {
  font-size: 26px;
  margin-top: 0;
  margin-bottom: 10px;
}

.description__wrapper:not(:last-child) {
  margin-bottom: 10px;
}

.description__term {
  display: inline;
  color: #656565;
}

.description__definition {
  display: inline;
  margin: 0;
  color: #000;
  font-weight: 700;
}

.header {
  margin-bottom: 20px;
  padding: 10px;
}

.header__wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.header__form {
  position: relative;
  display: flex;
  align-items: center;
}

.header__input {
  cursor: text;
  color: #656565;
  padding: 5px;
  padding-right: 24px;
  border: none;
  border-bottom: 1px solid #d5d5d5;
}
.header__input:hover {
  outline: 1px solid #d5d5d5;
}
.header__input:active {
  outline: 1px solid #000;
}

.header__button {
  position: absolute;
  right: 0;
  width: 24px;
  height: 24px;
  background-color: transparent;
  border: none;
  width: 24px;
  height: 24px;
  background-image: url("../img/search.png");
  background-repeat: no-repeat;
}

.header__toggle {
  cursor: pointer;
  width: 43px;
  height: 43px;
  background-color: #f6f6f6;
  background-image: url("../img/menu_open.png");
  background-size: 24px 24px;
  background-position: center;
  background-repeat: no-repeat;
  border: 1px solid #d5d5d5;
  border-radius: 5px;
}
.header__toggle:hover {
  outline: 1px solid #216fad;
}
.header__toggle:active {
  outline: 1px solid #000;
}

.info {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: nowrap;
  background-color: #f6f6f6;
  display: flex;
  flex-wrap: nowrap;
  padding: 0;
  padding-top: 20px;
  gap: 10%;
  overflow: hidden;
  margin-bottom: 10px;
}

.info__item {
  width: 40%;
}

.info__link {
  cursor: pointer;
  font-size: 16px;
  font-weight: 700;
  border: none;
  background-color: transparent;
  white-space: nowrap;
  color: #656565;
  text-transform: uppercase;
}
.info__link--current {
  color: #216fad;
  padding-bottom: 20px;
  border-bottom: 3px solid #216fad;
}
.info__link:hover {
  color: #216fad;
}
.info__link:active {
  color: #000;
}

.messenger {
  align-items: center;
}

.messenger__note {
  font-size: 14px;
  width: 130%;
}

.messenger__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  margin-left: 40px;
  justify-content: space-between;
}

.messenger__item {
  cursor: pointer;
  background-repeat: no-repeat;
  background-size: 34px 34px;
  background-position: center;
  width: 34px;
  height: 34px;
}
.messenger__item--whatsapp {
  background-image: url("../img/WhatsApp.png");
}
.messenger__item--viber {
  background-image: url("../img/Viber.png");
}
.messenger__item--telegram {
  background-image: url("../img/Telegram.png");
}
.messenger__item:hover {
  opacity: 0.5;
}

.page__body {
  min-width: 365px;
  max-width: 400px;
  margin: 0;
}
@media (min-width: 401px) {
  .page__body {
    margin: 0 auto;
  }
}

.product__title {
  font-size: 32px;
  margin-top: 0;
  margin-bottom: 10px;
  padding-left: 10px;
}

.product__slider-wrapper {
  position: relative;
  height: 345px;
  width: 355px;
  margin: 0 auto;
  margin-bottom: 20px;
}

.product__slider {
  position: relative;
  height: inherit;
  width: inherit;
  overflow: hidden;
}

.product__input {
  display: none;
}

.product__slide {
  height: inherit;
  opacity: 0;
  position: absolute;
  width: inherit;
  background-repeat: no-repeat;
}

.slide1 {
  background-image: url("../img/slab.png");
}

.slide2 {
  background-color: pink;
}

.slide3 {
  background-color: yellowgreen;
}

.slide4 {
  background-color: tomato;
}

.slide5 {
  background-color: orange;
}

.slide6 {
  background-color: violet;
}

.product__controls {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 15px;
  background-color: #dad4ce;
  padding: 2px;
  border-radius: 10px;
}

.product__label {
  cursor: pointer;
  position: relative;
  display: inline-block;
  height: 8px;
  width: 8px;
  border-radius: 50%;
  background-image: url(../img/radio.png);
  margin: 0 2px;
}

.product__label:hover,
#slide1:checked ~ .product__controls .product__label:nth-of-type(1),
#slide2:checked ~ .product__controls .product__label:nth-of-type(2),
#slide3:checked ~ .product__controls .product__label:nth-of-type(3),
#slide4:checked ~ .product__controls .product__label:nth-of-type(4),
#slide5:checked ~ .product__controls .product__label:nth-of-type(5),
#slide6:checked ~ .product__controls .product__label:nth-of-type(6) {
  background-image: url("../img/radio_checked.png");
}

#slide1:checked ~ .product__slider > .slide1,
#slide2:checked ~ .product__slider > .slide2,
#slide3:checked ~ .product__slider > .slide3,
#slide4:checked ~ .product__slider > .slide4,
#slide5:checked ~ .product__slider > .slide5,
#slide6:checked ~ .product__slider > .slide6 {
  opacity: 1;
}

.purchase {
  padding-left: 10px;
  padding-right: 10px;
  margin-bottom: 20px;
}

.purchase__main-container {
  display: flex;
  flex-direction: column;
  padding: 20px 10px;
  border: 2px solid #e7e7e7;
  border-radius: 5px;
}

.purchase__price {
  font-size: 16px;
  color: #656565;
  margin-bottom: 15px;
}
.purchase__price--main {
  font-size: 32px;
  font-weight: 700;
  color: #005aa2;
  margin-bottom: 5px;
}

.purchase__container {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.purchase__wrapper,
.purchase__form {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 10px;
}

.purchase__form {
  position: relative;
}

.purchase__input {
  font-family: inherit;
  font-weight: 700;
  width: 100%;
  border: 2px solid #e7e7e7;
  text-align: center;
  border-radius: 5px;
}

.purchase__input-button {
  cursor: pointer;
  display: block;
  position: absolute;
  width: 34px;
  height: 34px;
  border-radius: 50%;
  top: 50%;
  transform: translateY(-50%);
  border: none;
  background-position: center;
  background-repeat: no-repeat;
}
.purchase__input-button--plus {
  right: 54%;
  background-color: #005aa2;
  background-image: url("../img/Plus.png");
}
.purchase__input-button--minus {
  left: 4%;
  background-color: #f6f6f6;
  background-image: url("../img/minus.png");
}
.purchase__input-button:hover {
  opacity: 0.5;
}

.rating {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  gap: 5px;
  padding-left: 10px;
  margin-bottom: 10px;
}

.rating__item {
  width: 20px;
  height: 20px;
  background-size: 20px 20px;
  background-repeat: no-repeat;
  background-position: center;
}
.rating__item--full {
  background-image: url("../img/rating_full.png");
}
.rating__item--half {
  background-image: url("../img/rating_half.png");
}
.rating__item--empty {
  background-image: url("../img/rating_empty.png");
}